function Scroll() {
  let leftSide = document.querySelector('.employees');
  let currentX = 0;
  let currentY = 0;

  leftSide.parentElement.addEventListener('wheel', (event) => {
    event.preventDefault();
    let element = document.querySelector('#selected-letter > div');
    let width = document.querySelector('.left-side').offsetWidth;

    if (!element) {
      if (event.deltaY > 0) {
        currentX = Math.max(currentX - 220, -(5740 - width));
        leftSide.style.transform = 'translateX(' + currentX + 'px)';
        console.log(currentX + ' up');
      }
      if (event.deltaY < 0) {
        currentX = Math.min(currentX + 220, 0);
        leftSide.style.transform = 'translateX(' + currentX + 'px)';
        console.log(currentX + ' down');
      }
    }

    if (element) {
      if (event.deltaY > 0) element.scrollTop += 100;
      if (event.deltaY < 0) element.scrollTop -= 100;
    }
  });
}

export default Scroll;
